ul {
    list-style: none;
}

li {
  margin-bottom: 1rem;
}

.coverpage {
    margin: 0 2rem;
    align-content: center;
}

#cross-logo {
    display: block;
    max-width: 240px;
    height: auto;
    margin: auto;
}

.booktitle {
  text-align: center;
  margin-top: 0px
}

.booktitle a {
    color: inherit;
    text-decoration: none;
}

.booktitle b {
    font-size: x-large;
}

.booktitle small {
    font-size: x-small;
}

.card {
    height: 9.5rem;
    padding: 0.3rem 0.7rem;
    margin: auto 0.5rem;
    font-size: 1.6rem;
    text-align: center;
    /* Add shadows to create the "card" effect */
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    align-items: center;
}

.card small {
    color: darkred;
    mix-blend-mode: normal;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.3);
  }
  

  /* Add some padding inside the card container */
.container {
    padding: 0.1rem;
    align-items: center;
  }

.randomverse {
    width: 260px;
    margin: auto;
}

.randomverse p {
    font-size: 1.45rem;
    text-align: center;
}

.randomverse a {
    text-decoration: none;
    color: inherit;
}

.randomverse .bibleposition {
    color: darkred;
    padding: 0 1rem 0 1rem;
}

.navbar {
    overflow: hidden;
    min-height: 60px;
    z-index: 3;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    background: rgba(248, 249, 250, 0.9);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

.navbar a {
    color: inherit;
    text-decoration: none;
    display: flex;
}

.nav-item {
    background: transparent;
    box-sizing: border-box;
    position: relative;
    color: inherit;
    display: flex;
    margin: 0.5rem 1rem 0.5rem 0rem;
    padding: 0 0 0 0rem;
    justify-content: center;
    align-content: center;
    text-align: center;
    /* vertical-align: middle; */
}

.nav-item select {
    max-width: 50%;
    color: #363535;
    background: inherit;
    text-align: center;
    padding: 1rem 0.5rem 1rem 0.5rem;
    display: inline-block;
    height: fit-content;
    border-radius: 0.25em;
    font-size: var(--body-text);
}

.nav-item:first-child {
    margin: 0.5rem 2rem 0.5rem 45px;
}

.nav-item input {
    max-width: 50%;
    color: #363535;
    background: inherit;
    text-align: center;
    padding: 1rem 0rem 1rem 0rem;
    height: fit-content;
    border-style: solid;
    border-radius: 0.25em;
    font-size: var(--body-text);
}

.nav-item button {
    background: inherit;
    border-style: solid;
    border-radius: 2em;
    padding: 0.1rem 0.5rem 0.1rem 0.5rem;
    height: fit-content;
    margin-left: 0.5rem;
}

.vertical-center {
    margin: 0;
    position: absolute;
    top: 40%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.footerbar {
    overflow: hidden;
    min-height: 60px;
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    background: rgba(248, 249, 250, 0.9);
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}

.footer-item {
    background: transparent;
    box-sizing: border-box;
    color: inherit;
    display: inline-block;
    margin: 0.5rem 1rem 0.5rem 0;
    text-align: center;
    vertical-align: middle;
}

.footerbar svg:hover {
    background: #ddd;
    color: black;
}

.settings {
    width: 70%;
    margin: 30% auto;
    font-size: 2rem;
}

.settings-item {
    display: flex;
    justify-content: space-between;
}

.settings-item label {
    display: inline-block;
    margin: 20px 0px;
}

.settings-item select {
    display: inline-block;
    height: fit-content;
    margin: 15px;
    border-radius: 0.25em;
    font-size: 2rem;
}

.sharelinks {
    display: flex;
    max-width: 15rem;
    margin: auto;
    align-content: center;
    justify-content: space-between;
    margin-top: 1rem;
}

.toc {
    display: flex;
    width: 90%;
    margin: 5rem auto
}

.toc-card a {
    text-decoration: none;
    color: inherit
}

.toc-card-header {
     font-size: large;
     font-weight: bold;
     text-align: center;
     margin-bottom: 0.5rem;
 }

.verses-list {
    width: 90%;
 }

.verse-detail {
     margin-top: 0.5rem;
     line-height: normal;
     align-items: center;
 }

.verse-detail p {
     text-align: left;
     margin-bottom: 0;
 }

.verse-detail small {
     color: darkred;
     margin-left: 2rem;
 }

 .verse-detail em {
     margin-left: 0.5rem;
 }

 .verse-detail:hover {
    color: #efefef;
    background: #7d7d7d;
    /* mix-blend-mode: darken; */
    text-decoration: none;
 }

 .verse-detail a {
    color: inherit;
    text-decoration: none;
 }

.chapter-number {
      font-size: 4rem;
      color: darkred;
      mix-blend-mode: normal;
}

.chapter {
     justify-content: center;
     text-align: left;
     margin: 1rem 5% 7rem 5%;
     line-height: 2em;
 }

.chapter sup {
     color: darkred;
     mix-blend-mode: normal;
 }

 .close-page {
     position: absolute;
     top: 5px;
     right: 5px;
 }

.about-page {
     margin: 7rem 10rem;
 }

.about-text {
     margin-top: 2rem;
 }

.about-footer {
     font-style: italic;
     font-size: smaller;
     position: fixed;
     bottom: 2rem;
 }
